import { queryPositionPage, queryHistoryCloseOrderList, queryPBOOrderPage } from '@/api/trade'
import CheckAPI from '@/utils/checkAPI'
import { minus, toFixed, plus } from '@/utils/calculation'
import BigNumber from 'bignumber.js'

const EmptyProfitLossRang = {
    buyProfitRange: [], // 
    sellProfitRange: [], // 
    buyStopLossRange: [], // 
    sellStopLossRange: [], // 
}
const EmptyPendingPriceRang = {
    buyLimitRange: [], // 
    sellLimitRange: [], // 
    buyStopRange: [], // 
    sellStopRange: [], // 
}

let positionsConfig = {} // 
let pendingsConfig = {} // 

export default {
    namespaced: true,
    state: {
        modifyPositionId: 0, // ID
        pendingEnable: false, // 
        pendingPrice: 0, // 
        positionLoading: '', // 
        positionList: [], // 
        positionMap: {}, // map
        historyLoading: false, // 
        historyList: [], // 
        pendingList: [], // 
        pendingMap: {}, // 
        positionProfitLossList: [] // 
    },
    getters: {
        // 
        product (state, getters, rootState) {
            const productID = rootState._quote.productActivedID
            const product = rootState._quote.productMap[productID]
            return product
        },
        // 
        marketProfitLossRang (state, getters, rootState) {
            const product = getters.product
            if (!product) return EmptyProfitLossRang
            const curPosition = state.positionMap[state.modifyPositionId] // 
            const digits = product.price_digits
            const point = Math.pow(0.1, digits)
            const pip = point * product.pointRatio

            let buy_price = state.pendingPrice ? state.pendingPrice : product.buy_price
            let sell_price = state.pendingPrice ? state.pendingPrice : product.sell_price
            if (curPosition) {
                // 
                buy_price = sell_price = curPosition.direction === 1 ? product.sell_price : product.buy_price
            }

            const buyProfitMax = BigNumber(buy_price).plus(pip * product.stopLossMaxPoint).toFixed(digits) //  +pip*
            const buyProfitMin = BigNumber(buy_price).plus(pip * product.stopLossMinPoint).toFixed(digits) //  +pip*

            const sellProfitMax = BigNumber(sell_price).minus(pip * product.stopLossMinPoint).toFixed(digits) //  -pip*
            const sellProfitMin = BigNumber(sell_price).minus(pip * product.stopLossMaxPoint).toFixed(digits) //  -pip*

            const buyStopLossMax = BigNumber(buy_price).minus(pip * product.stopLossMinPoint).toFixed(digits) //  -pip*
            const buyStopLossMin = BigNumber(buy_price).minus(pip * product.stopLossMaxPoint).toFixed(digits) //  -pip*

            const sellStopLossMax = BigNumber(sell_price).plus(pip * product.stopLossMaxPoint).toFixed(digits) //  +pip*
            const sellStopLossMin = BigNumber(sell_price).plus(pip * product.stopLossMinPoint).toFixed(digits) //  +pip*

            return {
                buyProfitRange: [buyProfitMin, buyProfitMax], // 
                sellProfitRange: [sellProfitMin, sellProfitMax], // 
                buyStopLossRange: [buyStopLossMin, buyStopLossMax], // 
                sellStopLossRange: [sellStopLossMin, sellStopLossMax], // 
            }
        },
        // 
        pendingPriceRang (state, getters, rootState) {
            const product = getters.product
            if (!product || !state.pendingEnable) return EmptyPendingPriceRang
            const digits = product.price_digits
            const point = Math.pow(0.1, digits)
            const pip = point * product.pointRatio

            const buy_price = product.buy_price
            const sell_price = product.sell_price

            const buyLimitMax = BigNumber(buy_price).minus(pip * product.priceMinLimit).toFixed(digits) //  -pip*
            const buyLimitMin = BigNumber(buy_price).minus(pip * product.priceMaxLimit).toFixed(digits) //  -pip*

            const sellLimitMax = BigNumber(sell_price).plus(pip * product.priceMaxLimit).toFixed(digits) //  +pip*
            const sellLimitMin = BigNumber(sell_price).plus(pip * product.priceMinLimit).toFixed(digits) //  +pip*

            const buyStopMax = BigNumber(buy_price).plus(pip * product.priceMaxLimit).toFixed(digits) //  +pip*
            const buyStopMin = BigNumber(buy_price).plus(pip * product.priceMinLimit).toFixed(digits) //  +pip*

            const sellStopMax = BigNumber(sell_price).minus(pip * product.priceMinLimit).toFixed(digits) //  -pip*
            const sellStopMin = BigNumber(sell_price).minus(pip * product.priceMaxLimit).toFixed(digits) //  -pip*

            const priceMinLimit = product.priceMinLimit + 10
            const priceMinLimitPrice = toFixed(priceMinLimit * pip, digits)
            const defaultBuyPrice = minus(buy_price, priceMinLimitPrice) // 
            const defaultSellPrice = plus(sell_price, priceMinLimitPrice) // 
            return {
                buyLimitRange: [buyLimitMin, buyLimitMax], // 
                sellLimitRange: [sellLimitMin, sellLimitMax], // 
                buyStopRange: [buyStopMin, buyStopMax], // 
                sellStopRange: [sellStopMin, sellStopMax], // 
                defaultBuyPrice, // 
                defaultSellPrice, // 
            }
        }
    },
    mutations: {
        Empty_data (state, data) { // 
            state.positionList = []
            state.positionMap = {}
            state.historyList = []
            state.pendingList = []
            state.pendingMap = {}
            state.positionProfitLossList = []
        },
        Update_modifyPositionId (state, data) {
            state.modifyPositionId = data
        },
        Update_pendingEnable (state, data) {
            state.pendingEnable = data
        },
        Update_pendingPrice (state, data) {
            state.pendingPrice = data
        },
        Update_positionLoading (state, data) {
            state.positionLoading = data
        },
        Update_positionList (state, data) {
            state.positionList = data
            const positionMap = state.positionMap
            data.forEach(item => {
                if (!item || !item.positionId) return false
                const curPosition = positionMap[item.positionId]
                if (curPosition) Object.assign(item, { profitLoss: curPosition.profitLoss }) // mapitem
                positionMap[item.positionId] = item
            })
        },
        Update_historyLoading (state, data) {
            state.historyLoading = data
        },
        Update_historyList (state, data) {
            state.historyList = data
        },
        Update_pendingList (state, data) {
            state.pendingList = data
            const pendingMap = state.pendingMap
            data.forEach(item => {
                if (!item || !item.id) return false
                if (!pendingMap[item.id]) pendingMap[item.id] = {}
                Object.assign(pendingMap[item.id], item)
            })
        },
        Update_positionProfitLossList (state, dataList = []) {
            state.positionProfitLossList = dataList
            const positionMap = state.positionMap
            dataList.forEach(({ positionId, profitLoss }) => {
                if (positionMap[positionId]) positionMap[positionId].profitLoss = profitLoss
                else positionMap[positionId] = { profitLoss }
            })
        },
    },
    actions: {
        // 
        queryPositionPage ({ dispatch, commit, state, rootState }, params) {
            if (params) positionsConfig = params
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            // dispatch('queryPBOOrderPage')

            if (!accountListLen) {
                commit('Update_positionList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            commit('Update_positionLoading', true)
            return queryPositionPage(positionsConfig).then((res) => {
                commit('Update_positionLoading', false)
                if (res.check()) {
                    commit('Update_positionList', res.data)
                }
                return res
            })
        },
        // 
        queryHistoryCloseOrderList ({ dispatch, commit, state, rootState }, params = {}) {
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            if (!accountListLen) {
                commit('Update_historyList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            commit('Update_historyLoading', true)
            return queryHistoryCloseOrderList(params).then((res) => {
                commit('Update_historyLoading', false)
                if (res.check()) {
                    const newList = params.current === 1 ? res.data.list : state.historyList.concat(res.data.list)
                    commit('Update_historyList', newList)
                }
                return res
            })
        },
        // 
        queryPBOOrderPage ({ dispatch, commit, state, rootState }, params) {
            if (params) pendingsConfig = params
            const accountListLen = rootState._user.customerInfo?.accountList?.length
            if (!accountListLen) {
                commit('Update_pendingList', [])
                return Promise.resolve(new CheckAPI({ code: '0', data: [] })) // 
            }
            return queryPBOOrderPage(pendingsConfig).then((res) => {
                if (res.check()) {
                    commit('Update_pendingList', res.data)
                }
                return res
            })
        },
    }
}
